/* ========================================
   VIRTUAL TOUR PAGE - MODERN DESIGN
   ======================================== */

.virtual-tour-page {
  min-height: 100vh;
  background: #2a2a2a;
  display: flex;
  flex-direction: column;
  padding: 6rem 2rem 2rem;
  font-family: var(--font-family, 'Inter', sans-serif);
}

/* Unity Canvas - High Quality Rendering */
.unity-canvas {
  image-rendering: -webkit-optimize-contrast !important;
  image-rendering: crisp-edges !important;
  -ms-interpolation-mode: nearest-neighbor !important;
  image-rendering: pixelated !important;
  
  /* Override to auto for smooth rendering */
  image-rendering: auto !important;
  -webkit-font-smoothing: antialiased !important;
  -moz-osx-font-smoothing: grayscale !important;
  
  /* Ensure proper scaling */
  object-fit: contain;
  max-width: 100%;
  height: auto;
  
  /* Better rendering on high DPI displays */
  backface-visibility: hidden;
  -webkit-backface-visibility: hidden;
  transform: translateZ(0);
  -webkit-transform: translateZ(0);
}

.unity-container {
  width: 100%;
  max-width: 1600px;
  margin: 0 auto;
  padding: 0;
}

.unity-player {
  position: relative;
  width: 100%;
  background: #2a2a2a;
  border-radius: 12px;
  overflow: hidden;
  box-shadow: 0 10px 40px rgba(0, 0, 0, 0.5);
}

.tour-header {
  text-align: center;
  margin-bottom: 3rem;
  padding: 2rem 0;
  animation: fadeInUp 0.8s ease;
}

.tour-header h1 {
  font-size: 3.5rem;
  font-weight: 800;
  color: #ffffff;
  margin: 0 0 1.5rem;
  letter-spacing: -1px;
  line-height: 1.1;
}

.tour-header p {
  font-size: 1.2rem;
  color: rgba(255, 255, 255, 0.85);
  margin: 0;
  line-height: 1.7;
  max-width: 600px;
  margin-left: auto;
  margin-right: auto;
}

.tour-container {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  max-width: 1600px;
  width: 100%;
  margin: 0 auto;
  padding: 2rem 0;
}

.tour-controls {
  text-align: center;
  padding: 3rem 0;
  animation: fadeInUp 0.8s ease 0.2s;
  animation-fill-mode: both;
}

.btn-back-home {
  background: transparent;
  color: #ffffff;
  border: 2px solid #ffffff;
  padding: 1.125rem 2.5rem;
  border-radius: 10px;
  font-size: 1rem;
  font-weight: 700;
  cursor: pointer;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  text-transform: uppercase;
  letter-spacing: 0.5px;
  display: inline-flex;
  align-items: center;
  gap: 0.75rem;
}

.btn-back-home:hover {
  background: #ffffff;
  color: #2a2a2a;
  transform: translateY(-3px);
  box-shadow: 0 8px 20px rgba(255, 255, 255, 0.15);
}

.btn-back-home:active {
  transform: translateY(-1px);
}

/* Animations */
@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(30px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* ========================================
   MOBILE CONTROLS - RESPONSIVE
   ======================================== */

/* Hide mobile controls on desktop/laptop (screen width > 768px) */
@media (min-width: 769px) {
  .mobile-controls-container,
  .mobile-dpad-controls,
  .mobile-action-buttons,
  .btn-mobile-control,
  .btn-mobile-action,
  .mobile-instruction {
    display: none !important;
  }
  
  /* Show only desktop instruction */
  .desktop-instruction {
    display: block !important;
    margin: 0 !important;
  }
}

/* Show mobile controls on mobile (screen width <= 768px) */
@media (max-width: 768px) {
  .mobile-controls-container {
    display: block !important;
  }
  
  .mobile-dpad-controls,
  .mobile-action-buttons {
    display: flex !important;
  }
  
  .btn-mobile-control,
  .btn-mobile-action {
    display: inline-block !important;
  }
  
  .mobile-instruction {
    display: block !important;
  }
  
  /* Hide desktop instruction on mobile */
  .desktop-instruction {
    display: none !important;
  }
}

/* Mobile button styling */
.btn-mobile-control {
  padding: 1rem;
  font-size: 1.5rem;
  background: #1976d2;
  color: white;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  user-select: none;
  -webkit-tap-highlight-color: transparent;
  transition: all 0.2s ease;
}

.btn-mobile-control:active {
  background: #1565c0;
  transform: scale(0.95);
}

.btn-mobile-action {
  padding: 0.6rem 1.2rem;
  font-size: 0.9rem;
  touch-action: manipulation;
  -webkit-tap-highlight-color: transparent;
  transition: all 0.2s ease;
}

.btn-mobile-action:active {
  transform: scale(0.95);
}

/* Responsive Design */
@media (max-width: 768px) {
  .virtual-tour-page {
    padding: 5rem 1.5rem 1.5rem;
  }

  .tour-header {
    margin-bottom: 2rem;
    padding: 1rem 0;
  }

  .tour-header h1 {
    font-size: 2.5rem;
  }

  .tour-header p {
    font-size: 1rem;
  }

  .tour-controls {
    padding: 2rem 0;
  }

  .btn-back-home {
    padding: 1rem 2rem;
    font-size: 0.95rem;
  }
}

@media (max-width: 480px) {
  .tour-header h1 {
    font-size: 2rem;
  }

  .tour-header p {
    font-size: 0.95rem;
  }

  .btn-back-home {
    width: 100%;
    justify-content: center;
  }
}


